import React from "react";
import styled from "styled-components";
import LandingThumb from "../../assets/landing_thumb_02.svg";

const CreatorSlide = () => {
  return (
    <>
      <Background>
        <LandingThumbImg src={LandingThumb} />
        <TextContainer>
          <LabelText></LabelText>
          <MainText>
                {"\n"}   
          </MainText>
          <SubText>
                   ,  
                
          </SubText>
        </TextContainer>
      </Background>
    </>
  );
};

export default CreatorSlide;

const Background = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: auto;
  width: 100vw;
  height: 920px;
  background: #fff3ed;
`;

const TextContainer = styled.div`
  display: flex;
  flex-direction: column;
  margin-left: 138px;
`;

const LabelText = styled.div`
  font-family: Pretendard;
  font-style: normal;
  font-weight: bold;
  font-size: 26px;
  line-height: 26px;
  color: #ff6814;
  margin-bottom: 32px;
`;

const MainText = styled(LabelText)`
  width: 584px;
  height: 122px;
  font-size: 42px;
  line-height: 60px;
  color: #313338;
  margin-bottom: 44px;
  white-space: pre-wrap;
`;

const SubText = styled(LabelText)`
  width: 482px;
  height: 56px;
  font-weight: normal;
  font-size: 21px;
  line-height: 30px;
  color: #8b8b8b;
`;

const LandingThumbImg = styled.img`
  width: 746px;
`;
